.pop-up {
  position: absolute;
  border: 4px solid;
  border-radius: 12px;
  color: transparent;
  background-color: white;
  top: 8px;
  left: 37.5%;
  padding: 12px 24px;
  width: -webkit-max-content;
  width: max-content;
  max-width: 22ch;
  font-size: 1.25rem;
  text-align: center;
  -webkit-transform: translateY(-200%);
          transform: translateY(-200%);
}

@-webkit-keyframes popDown {
  0% {
    -webkit-transform: translateY(-200%);
            transform: translateY(-200%);
  }
  40% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  60% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  70% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  80% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  90% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-200%);
            transform: translateY(-200%);
  }
}

@keyframes popDown {
  0% {
    -webkit-transform: translateY(-200%);
            transform: translateY(-200%);
  }
  40% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  60% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  70% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  80% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  90% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-200%);
            transform: translateY(-200%);
  }
}

*::before,
*,
*::after {
  box-sizing: border-box;
  outline: 1px solid transparent;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: #606060;
  margin: 0;
  padding: 2rem;
  font-family: SF Pro Text;
  box-sizing: border-box;
}

#root {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  position: relative;
  padding: 1rem;
  overflow: auto;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 12px;
}

h1 {
  color: #6194bc;
  width: -webkit-max-content;
  width: max-content;
  border-bottom: 4px solid;
  border-right: 4px solid;
  padding-right: 5px;
  padding-bottom: 3px;
}

form {
  min-width: -webkit-max-content;
  min-width: max-content;
  max-width: 50%;
}

form div {
  display: flex;
  align-items: center;
}

input {
  margin: 5px 10px;
  font-size: 1rem;
}

label {
  font-weight: 500;
  margin: 5px 10px;
}

form div input[for="address"] {
  width: 38ch;
  height: 2rem;
  padding: 0 8px;
}

button {
  background-color: #a5d1f3;
  color: #202124;
  border: none;
  border-radius: 4px;
  font-size: inherit;
  padding: 4px 8px;
}

form > button {
  padding: 8px 16px;
  margin-top: 2rem;
  font-weight: 700;
}

button:hover,
button:active,
button:focus,
button:focus-within {
  background-color: #202124;
  color: #a5d1f3;
}

.person {
  width: -webkit-max-content;
  width: max-content;
  padding-right: 20px;
}

.person > p {
  color: #a5d1f3;
  display: inline-block;
  margin: 10px 20px;
}

.person .contact-name {
  text-transform: uppercase;
  background-color: #202124;
  padding: 2px 4px;
}

.phonebook-entries {
  max-height: 18rem;
  overflow-y: auto;
  border-top: 1px solid rgba(255, 255, 255, 0.25);
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: #202124;
}
::-webkit-scrollbar-thumb {
  background-color: #6194bc;
}

